%ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

%a {
  text-decoration: none;
  color: inherit;
}

%h1,
%h2,
%h3,
%h4,
%h5,
%h6 {
  padding: 0;
  margin: 0;
}

%p {
  padding: 0;
  margin: 0;
}

%li {
  padding: 0;
  margin: 0;
  list-style: none;
}
